, 44, 0.85);\n\n$success: #43a047;\n$success-light: lighten($success, 10);\n$success-dark: darken($success, 10);\n\n$pending: #ffb300;\n$pending-light: lighten($pending, 10);\n$pending-dark: darken($pending, 10);\n\n$error: #e53935;\n$error-light: lighten($error, 10);\n$error-dark: darken($error, 10);\n\n$coachmark-bg: #EBEBF6;\n$coachmark-dark-text: #1B1B19;\n$error-text: #fff;\n\n/**\n * Spacing\n */\n\n// 4px\n$spacing-smallest: 0.25rem;\n\n// 8px\n$spacing-xxsmall: 0.5rem;\n\n// 12px\n$spacing-xsmall: 0.75rem;\n\n// 16px\n$spacing-small: 1rem;\n\n// 20px\n$spacing-small-medium: 1.25rem;\n\n// 24px\n$spacing-medium: 1.5rem;\n\n// 28px\n$spacing-medium-large: 1.75rem;\n\n// 32px\n$spacing-large: 2rem;\n\n// 48px\n$spacing-xlarge: 3rem;\n\n// 64px\n$spacing-xxlarge: 4rem;\n\n// 96px\n$spacing-largest: 6rem;\n\n/*===================*\\\n||   Media Queries   ||\n\\*===================*/\n\n$device-mobile-size: 450px;\n$device-tablet-size: 800px;\n$device-laptop-size: 1024px;\n\n$device-landscape-mobile-size: 800px;\n$device-landscape-tablet-size: 1048px;\n$device-landscape-laptop-size: 1280px;\n\n\n:export {\n    primary: $primary;\n    secondaryLabel: $secondary-label;\n}\n\n/*===================*\\\n||   Icon sizes  ||\n\\*===================*/\n$icon-small: 16px;\n$icon-medium: 24px;\n$icon-large: 32px;\n$icon-xlarge: 48px;\n",".cdk-overlay-container,.cdk-global-overlay-wrapper{pointer-events:none;top:0;left:0;height:100%;width:100%}.cdk-overlay-container{position:fixed;z-index:1000}.cdk-overlay-container:empty{display:none}.cdk-global-overlay-wrapper{display:flex;position:absolute;z-index:1000}.cdk-overlay-pane{position:absolute;pointer-events:auto;box-sizing:border-box;z-index:1000;display:flex;max-width:100%;max-height:100%}.cdk-overlay-backdrop{position:absolute;top:0;bottom:0;left:0;right:0;z-index:1000;pointer-events:auto;-webkit-tap-highlight-color:transparent;transition:opacity 400ms cubic-bezier(0.25, 0.8, 0.25, 1);opacity:0}.cdk-overlay-backdrop.cdk-over